import React, { Component } from 'react';
import $ from 'jquery';
import imagesJson from "../data/images.json";
import audiosJson from "../data/audios.json";
import plantillasGenerales from '../data/documentos/lineamientos_planeamiento_generales.json';
import plantillasPreescolar from '../data/documentos/lineamientos_planeamiento_prescolar.json';
import plantillasPrimaria from '../data/documentos/lineamientos_planeamiento_primaria.json';
import plantillasSecundaria from '../data/documentos/lineamientos_planeamiento_secundaria.json';

class Modal extends Component {

  constructor () {
    super ();
    this.state = {
      htmlContent : ""      
    };
    this.selectTypeContent = this.selectTypeContent.bind(this);
    setTimeout( this.selectTypeContent, 10 );
  }

    images = imagesJson[0];
    audios = audiosJson[0];
    classModalBody = "modal-body"; // Se agrega "modal-body large cuando carga un pdf"
    modalAncho = "modal-dialog"; // clase que contiene el tamao del modal
    plantillasDePreescolar = plantillasPreescolar[0];
    plantillasDePrimaria = plantillasPrimaria[0];
    plantillasDeSecundaria = plantillasSecundaria[0];
    plantillasGenerales = plantillasGenerales[0];

    mostrarDiv (e){
      const opcion = e.target.title;
        $('#'+opcion).slideToggle("slow");
         
    }
 
  
  selectTypeContent () {    

    var tmpContent;
    
    switch (this.props.typeContent) {
      case "video":        
      //console.log("Video");       
        this.modalAncho =  this.modalAncho + " modal-lg";          
        tmpContent = <video controls width="100%" src={this.props.content} type="video/mp4" autoPlay  ></video>
      break;
      case "html":
      //console.log("html");        
        tmpContent = this.props.content
      break;
      case "audio":
      //console.log("audio");        
        tmpContent =  <audio controls  autoPlay src={this.props.content}></audio>                   
      break;
      case "pdf":
      //console.log("pdf"); 
      this.modalAncho =  this.modalAncho + " modal-lg";
      this.classModalBody = this.classModalBody + " modal-alto";
        tmpContent = <embed  src={this.props.content+"#toolbar=1" }    type='application/pdf' width='100%' height='100%'></embed>                  
      break;
      case "help":
      //console.log("pdf"); 
            this.modalAncho =  this.modalAncho + " modal-lg";
            this.classModalBody = this.classModalBody + " modal-alto";
              tmpContent =   
                    <React.Fragment>
                      <div>
                        <img  className="img-fluid"  src= {this.images.BgIndicaciones} alt="fondo indicaciones"/>  
                      </div>
                      <div className="texto-indicaciones">

                      <p>
                          Los recursos de esta caja de herramientas estn organizados en seis categoras con el propsito de que usted pueda dirigirse a la seccin ms atinente a su necesidad de informacin.  Estas categoras son:
                          </p>
                          <ul>
                            <li>Apoyos para el clima del aula</li>
                            <li>Apoyos para la evaluacin</li>
                            <li>Recursos didcticos</li>
                            <li>Documentos oficiales</li>
                            <li>Desarrollo profesional</li>
                            <li>Apoyos para el planeamiento</li>
                          </ul>
                          <p>
                          Para disponer de los distintos recursos, d un clic a los diferentes accesorios que acompaan a la imagen de la educadora.  Cada uno de estos objetos representa una de las categoras.  <br/>
                          Dentro de cada categora encontrar una descripcin de la seccin, as como una serie de accesos a documentos, enlaces, plantillas, y otros materiales de utilidad para su quehacer docente. 
                          Al colocar el puntero del mouse sobre cada acceso, podr leer la descripcin del recurso especfico que se le est proveyendo. <br/>
                          En las diferentes pantallas que acceda, dispondr de botones que le permitir retornar a la pantalla anterior o volver al men principal.
                          </p>

                      </div>
                      <img  className="img-ico-audio"  src= {this.images.Audio} onClick={this.playAudio}  alt="Icono activar audio"/>                                         
                      <audio src={this.audios.intro}  id="audioIntro" ></audio>                                                       
                    </React.Fragment>
      break;
       case "defHabilidades" :
        this.modalAncho =  this.modalAncho + " modal-lg";
        this.classModalBody = this.classModalBody + " modal-alto";
        tmpContent = ( <React.Fragment>
                          <h2 className="text-center">Concepto de Habilidades</h2>                        
                          <h3 className="text-center">Ministerio de Educacin Pblica</h3>
                          <br/>
                          <div className="container">
                            <div className="row ">
                                <div className="col-12 text-justify">
                                  Las habilidades son capacidades aprendidas por la poblacin estudiantil,  que utiliza para enfrentar situaciones problemticas de la vida diaria.  Estas se adquieren mediante el aprendizaje de la experiencia directa, a travs del modelado o la imitacin, por lo que trasciende la simple transmisin del conocimiento, lo cual promueve la visin y  formacin integral  de las personas, de cmo apropiarse del conocimiento sistematizado para crear su propio aprendizaje.
                                </div>
                            </div>
                          
   
                          <br/>
                          <strong>Fuente</strong> <cite> :   Alfaro et al. Por qu un currculo por habilidades en la reforma curricular de Costa Rica.  Ministerio de Educacin Pblica, Direccin de Desarrollo Curricular.  </cite>
                          </div>         
                        </React.Fragment>
                      )
         break;

      case "opcOrientaciones":
        // this.modalAncho =  this.modalAncho + " modal-lg";
        //this.classModalBody = this.classModalBody + " modal-alto";
        tmpContent = (<React.Fragment>          
          <div className="container">
            <div className="row ">
              <div className="col-6 text-center">
                <a href="https://recursos.mep.go.cr/ws_faro/pdf/apoyos_planeamiento/orientaciones_mediacion_pedagogica.pdf" target="_blank"  rel="noopener noreferrer"> 
                  <img src="https://recursos.mep.go.cr/ws_faro/generales/ico_pdf.png" alt="pdf documento general"/>                 
                </a>
              </div>
              <div className="col-6 text-center">
                <a href="https://recursos.mep.go.cr/ws_faro/pdf/apoyos_planeamiento/orientaciones_adultos.pdf" target="_blank"  rel="noopener noreferrer"> 
                    <img src="https://recursos.mep.go.cr/ws_faro/generales/ico_pdf.png" alt="pdf jvenes y adultos"/>                  
                </a>
              </div>
            </div>
            <div className="row">
              <div className="col-6 text-center">
              <a href="https://recursos.mep.go.cr/ws_faro/pdf/apoyos_planeamiento/orientaciones_mediacion_pedagogica.pdf" target="_blank"  rel="noopener noreferrer"> 
                  Orientaciones para la mediacin generales
                </a>
              </div>
              <div className="col-6 text-center">
              <a href="https://recursos.mep.go.cr/ws_faro/pdf/apoyos_planeamiento/orientaciones_adultos.pdf" target="_blank"  rel="noopener noreferrer"> 
                  Orientaciones para la mediacin jvenes y adultos
              </a>
              </div>
            </div>

          </div>
        </React.Fragment>
        )
        break;
         
         case "refUtiles" :
          this.modalAncho =  this.modalAncho + " modal-lg";
          this.classModalBody = this.classModalBody + " modal-alto";
          tmpContent = ( <React.Fragment>                
                  <div className="row ">
                    <div className="col-12 text-center">
                      <img className="img-ref" src= "https://recursos.mep.go.cr/ws_faro/p07-desarrollo-profesional/titu-referencias.png"   alt="titulo"/>
                      <hr/><hr/>
                    </div>
                  </div>
                   <div className="row referencias text-justify">
                     <div className="col-12">
                      <p className="p-ref">
                        <a  href="https://recursos.mep.go.cr/ws_faro/pdf/desarrollo_prof/ref_utilies/lectura2017.pdf" target = "_blank"  rel="noopener noreferrer"> <strong> Lectura 2017</strong> </a>  y 
                        <a href="https://recursos.mep.go.cr/ws_faro/pdf/desarrollo_prof/ref_utilies/lectura2018.pdf" target = "_blank" rel="noopener noreferrer"><strong> Lectura 2018: </strong>  </a>
                         Se presenta una compilacin de actividades de mediacin pedaggica, propuestas por docentes de educacin primaria de doce Direcciones Regionales, por medio de las cuales se fortalece la comprensin lectora, respondiendo a los momentos de antes, durante y despus de la lectura. 
                      </p>                      
                      <p className="p-ref">
                      <a href="https://recursos.mep.go.cr/ws_faro/pdf/desarrollo_prof/ref_utilies/escritura2017.pdf" target = "_blank" rel="noopener noreferrer"><strong>Escritura 2017:</strong>  </a>
                      Se presenta una compilacin de actividades de mediacin pedaggica, propuestas por docentes de educacin primaria de doce Direcciones Regionales, que fomentan la escritura en el estudiantado, en concordancia con el programa de estudio.
                      </p> 
                      <hr/>
                      <h5 className="tit_ref2"><strong>Dispositivos Mviles para la Educacin:</strong> </h5>
                      <p className="p-ref">                     
                          <a href="https://multimedia.uned.ac.cr/pem/aprendizaje_moviles/pag/conceptos.html" target = "_blank" rel="noopener noreferrer"><strong>Aprendizajes Mviles:</strong> </a> Este recurso publicado por la UNED de Costa Rica presenta las principales caractersticas de los dispositivos mviles, por un lado, la ubicuidad, en la que se aprende desde cualquier momento y lugar, por otro lado, la adaptabilidad o flexibilidad de estos dispositivos gracias a la conexin e ideas de cmo aprovecharlas en el rea educativa.
                      </p>
                    </div>
                  </div>



          </React.Fragment>
          )
           break;  

    
      default:
        console.log("Opcion fuera de rango en modal");        
      break;
    }

    this.setState({ 
      htmlContent: tmpContent      
    }, () => {   
       //console.log("Cambio estado contenido");
       
    }); 

  }


  playAudio () {
    let audioIntro = document.getElementById("audioIntro");
    audioIntro.play();
  }



  render() {    
      
    return (      

      <div>
    <div className="modal fade show  element-top" id="modalScreen"   >
        <div className= {this.modalAncho}   role="document">
          <div className="modal-content">

        <div className="col-12 text-right">
                <button   onClick={this.props.closeModal}  type="button" className="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                      </button>
        </div>
   
            <div className= {this.classModalBody } >
                  {this.state.htmlContent}                  
            </div>
         
          </div>
        </div>
      </div>

      <div className="modal-backdrop fade show"></div>
      </div>
    );
  }
}

export default Modal;
